import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewHeaderBar.module.scss';

import { DeviceType } from '../mobilePreview/MobilePreview';
import MobilePreviewURLEntry from './mobilePreviewURLEntry/MobilePreviewURLEntry';
import MobilePreviewDeviceTypeSelector from './mobilePreviewDeviceTypeSelector/MobilePreviewDeviceTypeSelector';

export interface IMobilePreviewHeaderBarProps {
  onChangeURL: (value: string) => void;
  onExit: () => void;
  onClickURLSubmit: () => void;
  onChangeDeviceType: (type: DeviceType) => void;
}

export default class MobilePreviewHeaderBar extends React.Component<IMobilePreviewHeaderBarProps, void> {

  public render(): React.ReactElement<IMobilePreviewHeaderBarProps> {

    const mobilePreviewURLEntry: React.ReactElement<MobilePreviewURLEntry> =
      <MobilePreviewURLEntry
        onChangeURL={ this.props.onChangeURL }
        onClickURLSubmit={ this.props.onClickURLSubmit }
      />;

  const mobilePreviewDeviceTypeSelector: React.ReactElement<MobilePreviewDeviceTypeSelector> =
      <MobilePreviewDeviceTypeSelector
        onChangeDeviceType={ this.props.onChangeDeviceType }
      />;

    const mobilePreviewNavBar: string = css(styles.mobilePreviewNavBar, 'ms-fontColor-neutralPrimary',
      'ms-bgColor-neutralLighter');
    const navBarItems: string = css('ms-Grid-row', styles.navBarItems);
    const navBarItemSmClass: string = css('ms-Grid-col', 'ms-u-sm6', 'ms-u-md6', 'ms-u-lg3', styles.navBarItem);
    const navBarItemMdClass: string = css('ms-Grid-col', 'ms-u-hiddenMdDown', 'ms-u-lg6', styles.navBarItem);
    const navBarItemRightClass: string = css(styles.navBarItem, styles.navBarItemRight);
    const xClass: string = css('ms-Icon', 'ms-Icon--x', 'ms-fontColor-neutralTertiary', styles.mobilePreviewXIcon,
      'ms-borderColor-neutralTertiaryAlt');

    return(
      <div className={ mobilePreviewNavBar }>
        <ul className={ navBarItems }>
          <li className={ navBarItemSmClass }>
            <span className={ styles.mobilePreviewTitle }>Mobile Preview</span>
          </li>
          <li className={ navBarItemMdClass }>
            { mobilePreviewURLEntry }
          </li>
          <div className='ms-Grid-col ms-u-sm6 ms-u-md6 ms-u-lg3'>
            <li className={ navBarItemRightClass }>
              <a onClick={ this.props.onExit }>
                <span className={ xClass }>
                </span>
              </a>
            </li>
            { mobilePreviewDeviceTypeSelector }
          </div>
        </ul>
      </div>
    );
  }
}